import React from "react";
import NavBar from "./navbargames";
import style from "./games.module.css";

function Games() {
  return (
    <div>
      <NavBar />

      <div className={style.gamesIntro}>
        <img src={require("./media/kebab-header.png")}></img>
      </div>

      <div className={style.gamesBody}>
        <h1>LATEST GAMES</h1>

        <div className={style.games}>
          <div id="item03" className={style.gamesItem}>
            <a href="/games/hackintosh" className="fill-div-games">
              <h1>HACK1NT0SH</h1>
              <p>Coming 23 Apr 2020</p>
            </a>
          </div>

          <div id="item02" className={style.gamesItem}>
            <a
              href="https://play.google.com/store/apps/details?id=com.Bidoniera.DontYouSlip"
              target="_blank"
              className="fill-div-games"
            >
              <h1>Don't you Slip!</h1>
            </a>
          </div>

          <div id="item01" className={style.gamesItem}>
            <a
              href="https://store.steampowered.com/app/745400/Kebab_it_Up/"
              target="_blank"
              className="fill-div-games"
            >
              <h1>Kebab it Up!</h1>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Games;
