import React from "react";
import NavBar from "./navbargames";
import style from "./hackintosh.module.css";

function Hackintosh() {
  return (
    <div>
      {/* <NavBar /> */}
      <div className={style.topBar}>
        <div className={style.backButton}>
          <a className={style.fillDiv} href="../games">
            <div className={style.backButtonSvg}></div>
            <h1>Back to games</h1>
          </a>
        </div>
      </div>
      <div className={style.hackintosh}>
        <div className={style.uiBorder}>
          <h1>CODENAME: HACK1NT0SH</h1>
          <h2>COMING APRIL 23RD, 2020</h2>
        </div>

        <div className={style.divider1}>
          <div className={style.about}>
            <h1>FEATURES</h1>

            <div className={style.aboutInner}>
              <h1>PLAY AS A SKILLED HACKER IN A FUTURISTIC WORLD</h1>
              <h1>MAKE YOUR WAY THROUGH HIGH SECURITY LABORATORIES</h1>
              <h1>STEAL TOP-SECRET DATA AND USE IT AGAINST YOUR ENEMY</h1>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Hackintosh;
