import React from "react";
import { Controller, Scene } from "react-scrollmagic";
import NavBar from "./navbar";

function Home() {
  var upcomingName = "HACK1NT0SH";

  return (
    <div>
      <NavBar />

      <Controller>
        <Scene pin triggerHook={0}>
          <div className="intro">
            <video autoPlay muted loop id="bg-video">
              <source
                src={require("./media/Kebab Website Video.mp4")}
                type="video/mp4"
              ></source>
            </video>

            <h1>BIDONIERA</h1>
          </div>
        </Scene>
      </Controller>

      <section>
        <div className="site-body">
          <h1>CHECK OUT OUR GAMES</h1>

          <Controller>
            <Scene
              triggerHook={0.8}
              triggerElement="#ucg"
              classToggle={["#ucg", "fade-in"]}
            >
              <div id="ucg" className="upcoming-game">
                <a className="fillDivUPG" href="/games/hackintosh">
                  <div className="ug-img"></div>

                  <div className="ug-text">
                    <h1>UPCOMING: {upcomingName}</h1>
                    <p>PLAY AS A SKILLED HACKER IN A FUTURISTIC WORLD</p>
                    <p>MAKE YOUR WAY THROUGH HIGH SECURITY LABORATORIES</p>
                    <p>STEAL TOP-SECRET DATA AND USE IT AGAINST YOUR ENEMY</p>
                    <p className="rls-date">Release Date: 23rd April, 2020</p>
                  </div>
                </a>
              </div>
            </Scene>
          </Controller>

          <div className="games">
            <Controller>
              <Scene
                triggerHook={0.8}
                triggerElement="#item01"
                classToggle={["#item01", "fade-in"]}
              >
                <div id="item01" className="item">
                  <a
                    href="https://store.steampowered.com/app/745400/Kebab_it_Up/"
                    target="_blank"
                    className="fill-div-games"
                  >
                    <h1>Kebab it Up!</h1>
                  </a>
                </div>
              </Scene>

              <Scene
                triggerHook={0.8}
                triggerElement="#item02"
                classToggle={["#item02", "fade-in"]}
              >
                <div id="item02" className="item">
                  <a
                    href="https://play.google.com/store/apps/details?id=com.Bidoniera.DontYouSlip"
                    target="_blank"
                    className="fill-div-games"
                  >
                    <h1>Don't you Slip!</h1>
                  </a>
                </div>
              </Scene>

              <Scene
                triggerHook={0.8}
                triggerElement="#item03"
                classToggle={["#item03", "fade-in"]}
              >
                <div id="item03" className="item">
                  <a href="/games/hackintosh" className="fill-div-games">
                    <h1>{upcomingName}</h1>
                    <p>Coming 23 Apr 2020</p>
                  </a>
                </div>
              </Scene>
            </Controller>
          </div>

          <div className="bid-button">
            <a className="fill-div" href="games">
              View All
            </a>
          </div>
        </div>

        {/* <div class="mask-colophon"></div> */}
      </section>

      <div className="about">
        <div className="about-inner">
          <h1>ABOUT US</h1>
          <div className="about-text-div">
            <p>
              We are an independent video game developer team whose goal is to
              create fun and quirky games that all of us can enjoy.
            </p>
            <p>
              It started out as a fun little hobby back in 2014 and since, we’ve
              released games for both PC and mobile.
            </p>
            <p>
              We’re all tired of scummy pay-to-win games that no one, except for
              the publisher wants.
            </p>
          </div>
        </div>
      </div>

      <section>
        <div id="contact" className="social-media">
          <div className="social-contact-container">
            <div className="contact-inner" id="contact">
              <h1>CONTACT</h1>
              <a href="mailto: abc@example.com">contact@bidoniera.com</a>
              <a href="mailto: abc@example.com">jobs@bidoniera.com</a>
            </div>

            <div className="social-media-inner">
              <h1>SOCIAL MEDIA</h1>
              <div className="social-media-button">
                <a className="social-twitter social-btn" href="about.html"></a>
              </div>
              <div className="social-media-button">
                <a className="social-facebook social-btn" href="about.html"></a>
              </div>
              <div className="social-media-button">
                <a
                  className="social-instagram social-btn"
                  href="about.html"
                ></a>
              </div>
              <div className="social-media-button">
                <a
                  className="social-youtube social-btn"
                  href="https://www.youtube.com/channel/UCjVEFYXCsK59yzoTof5TeQQ"
                ></a>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Home;
