import React from "react";
import { Controller, Scene } from "react-scrollmagic";
import { Link, animateScroll as scroll } from "react-scroll";

function NavBarGames() {
  return (
    <div>
      <Controller>
        <Scene pin triggerHook={0}>
          <div className="nav-bar">
            <img src={require("./media/logo-transparent.png")}></img>

            <div className="nav-button">
              <a href="/">HOME</a>
            </div>

            <div className="nav-button-act">
              <p>GAMES</p>
            </div>

            <div className="nav-button">
              {/* <a href="#about">CONTACT</a> */}
              <Link
                className="smooth-link"
                to="about"
                smooth={true}
                duration={300}
              >
                CONTACT
              </Link>
            </div>

            <div className="nav-button">
              <a href="about.html">Y'ALL LIKE KEBABS?</a>
            </div>

            <div className="nav-button">
              <a href="about.html">SUPPORT</a>
            </div>
          </div>
        </Scene>
      </Controller>
    </div>
  );
}

export default NavBarGames;
